import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations';
import * as actions from './actions.js';
import * as getters from './getters.js';
import { cloneObject } from './util.js';

Vue.use(Vuex);
const mockdata = [
  {
    'questionnaireId': 'c68ebba5-b176-49e0-b91d-07ff389d226a',
    'title': '',
    'status': '',
    'time': '2017531',
    'questions': [
      {
        'type': 'radio',
        'content': '',
        'options': ['', '']
      },
      {
        'type': 'radio',
        'content': '',
        'options': ['', '', '', '']},
      {
        'type': 'checkbox',
        'content': '',
        'options': ['', '', '']
      },
      {
        'type': 'text',
        'content': '',
        'isRequired': true
      }
    ],
    'data': [
      [0, 0, [1, 0], ''],
      [1, 1, [1, 2], ''],
      [1, 2, [0], ''],
      [1, 3, [1, 0], ''],
      [1, 1, [1, 2], '']
    ]
  },
  {
    'questionnaireId': '2cc7b14b-a166-443e-a022-222052053905',
    'title': '',
    'status': '',
    'time': '2017429',
    'questions': [
      {
        'type': 'radio',
        'content': '',
        'options': ['', '']
      },
      {
        'type': 'checkbox',
        'content': '',
        'options': ['', '', '', '', '']
      },
      { 'type': 'text',
        'content': '',
        'isRequired': false
      }
    ],
    'data': []
  }
];
localStorage.setItem('list', JSON.stringify(mockdata));
const list = JSON.parse(localStorage.list);
// 
export const initEditing = {
  questionnaireId: -1,
  title: '',
  status: '',
  time: 0,
  questions: [],
  data: []
};
export const status = {
  isChooseType: false,
  isReasonableData: true,
  isReasonableTime: false,
  isReasonableQn: false
};
const state = {
  list,
  editing: cloneObject(initEditing),
  status: cloneObject(status)
};

export default new Vuex.Store({
  state,
  mutations,
  actions,
  getters
});



